body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(135deg, #fceff9, #e0c3fc);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.app {
  text-align: center;
  padding: 2rem;
  background-color: white;
  border-radius: 2rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 90%;
}

h1 {
  font-size: 2.5rem;
  color: #c85cc3;
  margin-bottom: 0.5rem;
}

h2 {
  font-size: 1.5rem;
  color: #7d4b94;
  margin-bottom: 1.5rem;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 80px);
  gap: 15px;
  justify-content: center;
  margin-bottom: 2rem;
}

.square {
  width: 80px;
  height: 80px;
  border-radius: 15px;
  background-color: #f4e3f3;
  border: 2px solid #d5aedc;
  transition: 0.3s ease;
  cursor: pointer;
}

.square:hover {
  background-color: #f2d1f5;
}

.square.active {
  background-color: #e7bafc;
  border: 2px solid #b17db9;
  box-shadow: 0 0 10px #d38cf9;
}

.status {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 1.5rem;
  min-height: 2rem;
}

.btn {
  background-color: #d38cf9;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 1rem;
  margin: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #b86be6;
}


